import fetch from 'isomorphic-unfetch';
import { Charts, ChartContainer, ChartRow, YAxis, Resizable, BarChart } from "react-timeseries-charts";
import { TimeSeries, TimeRange, Index as PondIndex } from "pondjs";

const ChartForCity = (props: {data: any, city: string}) => {
  const city = props.data.filter(e => e.includes(props.city));
  const data = city.map(e => [e[0], e[4]]);

  const timeseries = new TimeSeries({
    name: "corona",
    columns: ["index", "cases"],
    points: data.map(([d, value]) => [
        (PondIndex.getIndexString as any)("1h", new Date(d)),
        value
    ])
  });

  return <div>
    <Resizable>
      <ChartContainer timeRange={timeseries.timerange()} >
        <ChartRow height="150">
          <YAxis
            id="cases"
            label="Coronavirus cases"
            min={0}
            max={timeseries.max('cases')}
            type="linear"
          />
          <Charts>
            <BarChart
              axis="cases"
              columns={["cases"]}
              series={timeseries}
            />
          </Charts>
        </ChartRow>
      </ChartContainer>
    </Resizable>
  </div>
}

const Index = (props: { data: any }) => {
  return <ChartForCity city='San Francisco' />
}

